.TodoSearch {
  background: white;
  color: black;
  border-radius: 20px;
  border: 2px solid black;
  margin: 0 24px;
  height: 64px;
  width: 80vw;
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  color: black;
  font-family: 'Montserrat';
  font-weight: 400;
}
